import React from 'react';

import { makeStyles, withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';

import Title from '~/components/Title';
import Icon from '~/components/Specialties/Icon';

const useStyles = makeStyles((theme) => ({
  root: {
    minHeight: '431px',
    height: 'auto',
    flexGrow: 1,
    backgroundColor: '#f8f7ee'
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
    width: '100%',
    height: '420px'
  },
}));

const SpecialtiesText = withStyles((theme) => ({
  root: {
    color: '#324b4a',
    fontSize: '18px',
    lineHeight: '1.56',
    fontWeight: '300',
    marginTop: '32px'
  },
}))(Typography);

const SpecialtiesNames = withStyles((theme) => ({
  root: {
    color: '#324b4a',
    fontSize: '16px',
    fontWeight: 'normal',
  },
}))(Typography);

export default function CenteredGrid() {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <Grid container direction="row" justify="flex-end" alignItems="center">
        <Grid item xs={2} />
        <Grid item xs={8} style={{marginTop: '80px'}}>
          <Title>Especialidades</Title>
          <SpecialtiesText component="div">Nam et aliquet leo. Cras interdum, tellus ut scelerisque lacinia, neque tortor hendrerit est, vitae porttitor nisi ante vitae risus. Sed rhoncus ullamcorper nisl eu dignissim.</SpecialtiesText>

          <Grid container direction="row" justify="space-between" alignItems="center" style={{marginTop: '40px'}}>
            <Grid item xs={12} md={4} style={{marginBottom: '50px'}}>
              <Grid container direction="row" alignItems="center">
                <Icon type="neurocirurgia" />
                <SpecialtiesNames component="div" style={{marginRight: '20px'}}>Neurocirurgia</SpecialtiesNames>
              </Grid>
            </Grid>
            <Grid item xs={12} md={4} style={{marginBottom: '50px'}}>
              <Grid container direction="row" alignItems="center">
                <Icon type="dor_orofacial" />
                <SpecialtiesNames component="div" style={{float: 'right'}}>Dor Orofacial</SpecialtiesNames>
              </Grid>
            </Grid>
            <Grid item xs={12} md={4} style={{marginBottom: '50px'}}>
              <Grid container direction="row" alignItems="center">
                <Icon type="neurologia" />
                <SpecialtiesNames component="div" style={{float: 'right'}}>Neurologia</SpecialtiesNames>
              </Grid>
            </Grid>
          </Grid>  
        </Grid>
        <Grid item xs={2} />
      </Grid>
    </div>
  );
}
