import React from 'react';

import clsx from 'clsx';
import { useHistory } from "react-router-dom";
import { HashLink as Link } from 'react-router-hash-link';

import { withStyles, makeStyles, useTheme } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import List from '@material-ui/core/List';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import MailIcon from '@material-ui/icons/Mail';
import Hidden from '@material-ui/core/Hidden';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';

import Button from '~/components/Button';
import Icon from '~/components/Icon';

const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
  },
  title: {
    flexGrow: 1,
  },
  hide: {
    display: 'none',
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
  },
  drawerPaper: {
    width: drawerWidth,
  },
  drawerHeader: {
    display: 'flex',
    alignItems: 'center',
    padding: theme.spacing(0, 1),
    // necessary for content to be below app bar
    ...theme.mixins.toolbar,
    justifyContent: 'flex-start',
  },
}));

const useToolbarStyles = makeStyles((theme) => ({
  root: {
    height: '120px'
  }
}));

const useTypographStyles = makeStyles((theme) => ({
  root: {
    fontSize: '14px',
    fontWeight: 'bold',
    fontStretch: 'normal',
    fontStyle: 'normal',
    lineHeight: 'normal',
    letterSpacing: 'normal',
  }
}));

const useAppbarStyles = makeStyles((theme) => ({
  transition: theme.transitions.create(['margin', 'width'], {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
  }),
  appBarShift: {
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
    marginRight: drawerWidth,
  },
  colorPrimary: {
    backgroundColor: '#FFFFFF',
    color: theme.palette.primary.main
  },
}));

const Logo = withStyles((theme) => ({
  root: {
    padding: '15px',
    fontSize: '200px',
    '&:hover': {
      backgroundColor: 'transparent',
    },
  },
}))(IconButton);

export default function PersistentDrawerRight() {
  const classes = useStyles();
  const history = useHistory();
  const theme = useTheme();
  const appBarClasses = useAppbarStyles();
  const toolBarClasses = useToolbarStyles();
  const typographClasses = useTypographStyles();
  const [open, setOpen] = React.useState(false);

  const handleDrawerOpen = () => {
    setOpen(true);
  };

  const handleDrawerClose = () => {
    setOpen(false);
  };

  const scrollWithOffset = (el, offset) => {
    const elementPosition = el.offsetTop - offset;
    window.scroll({
      top: elementPosition,
      left: 0,
      behavior: "smooth"
    });    
  }

  return (
    <div className={classes.root}>
      <AppBar
        elevation={0}
        position="fixed"
        className={clsx(appBarClasses.appBar, appBarClasses.colorPrimary, {
          [appBarClasses.appBarShift]: open,
        })}
      >
        <Container maxWidth="lg" disableGutters={true}>
          <Toolbar className={clsx(toolBarClasses.root)} disableGutters>
            <Grid container item sm={4} lg={5} direction="row" justify="flex-start" alignItems="center"> 
              <Logo disableRipple onClick={() => history.push("http://localhost:3001/")}>
                <Icon fontSize="inherit" viewBox="0 0 175 48" type="logo" />
              </Logo>
            </Grid>

            <Hidden smDown>
              <Grid container item sm={8} lg={7} direction="row" justify="space-between" alignItems="center"> 
                <Grid>
                <Link to="/sobre">
                  <Typography color="primary" className={typographClasses.root}>
                    SOBRE
                  </Typography>
                  </Link>
                </Grid>
                <Grid>
                  <Link 
                    to="/especialidades#tratamentos"
                    scroll={el => scrollWithOffset(el, 150)}
                    >
                    <Typography color="primary" className={typographClasses.root}>
                      TRATAMENTOS
                    </Typography>
                  </Link>
                </Grid>
                <Grid>
                  <Link to="/especialidades">
                    <Typography color="primary" className={typographClasses.root}>
                      ESPECIALIDADES
                    </Typography>
                  </Link>
                </Grid>
                <Grid>
                  <Link to="/contato">
                    <Typography color="primary" className={typographClasses.root}>
                      CONTATO
                    </Typography>
                  </Link>
                </Grid>
                <Grid>
                  <Button onClick={() => history.push("https://api.whatsapp.com/send?phone=5581988713651")} style={{marginRight: '15px'}} startIcon={<Icon type="whatsapp" style={{fontWeight: 'bold'}} />}>81 98871.3651</Button>
                </Grid>
              </Grid>
            </Hidden>  

            <Hidden mdUp>
            <Grid container item sm={8} direction="row" justify="flex-end" alignItems="center"> 
              <IconButton
                color="inherit"
                aria-label="open drawer"
                edge="end"
                onClick={handleDrawerOpen}
                className={clsx(open && classes.hide)}
              >
                <MenuIcon fontSize="large" />
              </IconButton>
              </Grid>
            </Hidden>
          </Toolbar>
        </Container>
      </AppBar>
      <Drawer
        className={classes.drawer}
        variant="persistent"
        anchor="right"
        open={open}
        classes={{
          paper: classes.drawerPaper,
        }}
      >
        <div className={classes.drawerHeader}>
          <IconButton onClick={handleDrawerClose}>
            {theme.direction === 'rtl' ? <ChevronLeftIcon /> : <ChevronRightIcon />}
          </IconButton>
        </div>
        <Divider />
        <List>
          {['Inbox', 'Starred', 'Send email', 'Drafts'].map((text, index) => (
            <ListItem button key={text}>
              <ListItemIcon>{index % 2 === 0 ? <InboxIcon /> : <MailIcon />}</ListItemIcon>
              <ListItemText primary={text} />
            </ListItem>
          ))}
        </List>
        <Divider />
        <List>
          {['All mail', 'Trash', 'Spam'].map((text, index) => (
            <ListItem button key={text}>
              <ListItemIcon>{index % 2 === 0 ? <InboxIcon /> : <MailIcon />}</ListItemIcon>
              <ListItemText primary={text} />
            </ListItem>
          ))}
        </List>
      </Drawer>
    </div>
  );
}
