import React from 'react';

import styled from 'styled-components';
import { useHistory } from 'react-router-dom';
import clsx from 'clsx';

import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import MagicSliderDots from 'react-magic-slider-dots';
import 'react-magic-slider-dots/dist/magic-dots.css';

import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Link from '@material-ui/core/Link';
import Hidden from '@material-ui/core/Hidden';

import Title from '~/components/Title';
import Img136x136 from '~/assets/img/136x136.png';
import Web from '~/components/Team/Web';
import Image from '~/components/Team/Image';
import Text from '~/components/Team/Text';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  marginTop: {
    marginTop: '49px'
  },
  marginBottom: {
    marginBottom: '80px'
  },
  centerBox: {
    left: 'calc(50% - (152px / 2))'
  }
}));

const DivTeamMate = styled.div`
  max-width: 170px;
  height: auto;
  display: flex;
  left: calc(50% - (170px / 2));
  position: relative;
`;

export default function CenteredGrid() {
  const classes = useStyles();
  const history = useHistory();

  const settings = {
    dots: true,
    arrows: false,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    appendDots: dots => {
      return <MagicSliderDots dots={dots} numDotsToShow={4} dotWidth={30} />;
    }
  };

  return (
    <div className={classes.root}>
      <Grid container direction="row">
        <Grid item xs={1} />
        <Grid item xs={10}>
          <Grid container direction="row" alignItems="center" className={classes.marginTop}>
            <Grid item xs={8}>
              <Title>Quem faz</Title>
            </Grid>
            <Grid item xs={4}>
              <Link href="#" onClick={() => history.push('sobre#team')} underline="always" style={{float: 'right'}}>ver toda equipe</Link>
            </Grid>
          </Grid>
        </Grid>
        <Grid item xs={1} />
      </Grid>

      <Hidden mdUp> 
        <Slider {...settings} className={clsx(classes.marginTop, classes.marginBottom)}>
          <DivTeamMate>
            <Image>{Img136x136}</Image>
            <Text>Dr. Claudio Henrique Fernandes Vidal</Text>
          </DivTeamMate>
          <DivTeamMate>
            <Image>{Img136x136}</Image>
            <Text>Dr. Claudio Henrique Fernandes Vidal</Text>
          </DivTeamMate>
          <DivTeamMate>
            <Image>{Img136x136}</Image>
            <Text>Dr. Claudio Henrique Fernandes Vidal</Text>
          </DivTeamMate>
          <DivTeamMate>
            <Image>{Img136x136}</Image>
            <Text>Dr. Claudio Henrique Fernandes Vidal</Text>
          </DivTeamMate>
          <DivTeamMate>
            <Image>{Img136x136}</Image>
            <Text>Dr. Claudio Henrique Fernandes Vidal</Text>
          </DivTeamMate>
        </Slider>
      </Hidden>

      <Hidden smDown>
        <Web parent="home" />
      </Hidden>
    </div>
  );
}
