import React from 'react';

import { makeStyles } from '@material-ui/core/styles';
import Popper from '@material-ui/core/Popper';

const useStyles = makeStyles((theme) => ({
  typography: {
    padding: theme.spacing(2),
  },
  paper: {
    border: '1px solid #bfb653',
    backgroundColor: 'white',
    width: '370px',
    padding: '20px',
    borderRadius: '8px',
  },
  arrow: {
    zIndex: 1,
    position: 'absolute',
    fontSize: 7,
    width: '3em',
    height: '3em',
    '&::before': {
      content: '""',
      margin: 'auto',
      display: 'block',
      width: 0,
      height: 0,
      borderStyle: 'solid',
    },
  },
  popper: {
    // marginTop: '10px',
    zIndex: 1,
    '&[x-placement*="bottom"] $arrow': {
      top: 0,
      left: 0,
      marginTop: '-0.9em',
      width: '3em',
      height: '1em',
      '&::before': {
        borderWidth: '0 1em 1em 1em',
        borderColor: `${theme.palette.background.paper} #bfb653 ${theme.palette.background.paper} #bfb653`,
      },
    },
    '&[x-placement*="top"] $arrow': {
      bottom: 0,
      left: 0,
      marginBottom: '-0.9em',
      width: '3em',
      height: '1em',
      '&::before': {
        borderWidth: '1em 1em 0 1em',
        borderColor: `transparent transparent transparent transparent`,
      },
    },
    '&[x-placement*="right"] $arrow': {
      left: 0,
      marginLeft: '-0.9em',
      height: '3em',
      width: '1em',
      '&::before': {
        borderWidth: '1em 1em 1em 0',
        borderColor: `${theme.palette.background.paper} #bfb653 #bfb653 #bfb653`,
      },
    },
    '&[x-placement*="left"] $arrow': {
      right: 0,
      marginRight: '-0.9em',
      height: '3em',
      width: '1em',
      '&::before': {
        borderWidth: '1em 0 1em 1em',
        borderColor: `${theme.palette.background.paper} #bfb653 #bfb653 #bfb653`,
      },
    },
  },
}));

export default function SimplePopover(props) {
  const classes = useStyles();

  return (
    <div>
      <Popper 
        // id={props.id} 
        open={props.open} 
        anchorEl={props.anchorEl}
        disablePortal={true}
        className={classes.popper}
        modifiers={{
          arrow: {
            enabled: true,
            element: props.arrowRef,
          },
        }}
      >
        <span className={classes.arrow} ref={props.setArrowRef} />
        <div className={classes.paper}>The content of the Popper.</div>
      </Popper>
    </div>
  );
}
