import React from 'react';

import clsx from 'clsx';
import styled from 'styled-components';

import { makeStyles, withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';

import Img136x136 from '~/assets/img/136x136.png';

import Button from '~/components/Button';
import Text from '~/components/Team/Text';
import Popover from '~/components/Team/Popover';

const useStyles = makeStyles((theme) => ({
  marginTop: {
    marginTop: '49px'
  },
  marginBottom: {
    marginBottom: '80px'
  },
  centerBox: {
    left: 'calc(50% - (152px / 2))'
  },
}));

const DivImage = styled.div`
  background-color: #F8F7EE; 
  width: 152px !important;
  height: 152px;
  border-radius: 50%;
  border: solid 8px #F8F7EE;
  left: calc(50% - (152px / 2));
  position: relative;
  img {
    border-radius: 50%;
  }
`;

export default function Web(props) {
  const classes = useStyles();

  const [arrowRef, setArrowRef] = React.useState(null);
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [open, setOpen] = React.useState(false);

  const [id, setId] = React.useState(null);

  const handleClick = (event, id) => {
    setAnchorEl(anchorEl ? null : event.currentTarget);
    setOpen((prevOpen) => !prevOpen);
    setId(id);
  };

  const handleClose = () => {
    // setAnchorRef(null);
  };

  return <Grid container direction="row" justfy="center" className={clsx(classes.marginTop, classes.marginBottom)}>
    <Popover
      anchorEl={anchorEl}
      arrowRef={arrowRef}
      setArrowRef={setArrowRef}
      onClose={handleClose}
      open={open}
      id={id}
    />
    
    <Grid item xs={1} md={1} />
    <Grid item xs={5} md={2}>
      <Box className={classes.centerBox}>
        <DivImage>
          <img src={Img136x136} alt="" width="100%" height="100%" />
        </DivImage>
      </Box>
      <Text>Dr. Claudio Henrique Fernandes Vidal</Text>
      {props.parent === 'about' ? <Button type="team" ref={anchorEl} aria-describedby={'123456'} onClick={(event) => handleClick(event, '123456')}>+</Button> : null}
    </Grid>
    <Grid item xs={5} md={2}>
      <Box className={classes.centerBox}>
        <DivImage>
          <img src={Img136x136} alt="" width="100%" height="100%" />
        </DivImage>
      </Box>
      <Text align="center" component="div">Dra. Ana Claudia Soares Espíndola</Text>
      {props.parent === 'about' ? <Button type="team" aria-describedby={'12345'} onClick={(event) => handleClick(event, '12345')}>+</Button> : null}
    </Grid>
    <Grid item xs={5} md={2}>
      <Box className={classes.centerBox}>
        <DivImage>
          <img src={Img136x136} alt="" width="100%" height="100%" />
        </DivImage>
      </Box>
      <Text align="center" component="div">Dr. Dhyego Ferreira Moreira de Lacerda</Text>
    </Grid>
    <Grid item xs={5} md={2}>
      <Box className={classes.centerBox}>
        <DivImage>
          <img src={Img136x136} alt="" width="100%" height="100%" />
        </DivImage>
      </Box>
      <Text align="center" component="div">Dr. Bruno Lôbo Mota de Siqueira</Text>
    </Grid>
    <Grid item xs={5} md={2}>
      <Box className={classes.centerBox}>
        <DivImage>
          <img src={Img136x136} alt="" width="100%" height="100%" />
        </DivImage>
      </Box>
      <Text align="center" component="div">Dra. Silvana Sobreira Santos</Text>
    </Grid>
    <Grid item md={1} />
  </Grid>;
}